.node {
  width: 25px;
  height: 25px;
  border: 1px solid rgba(175, 216, 248, 0.9);
}

.start-node {
  background: url("../../../assets/start.svg") no-repeat center center;
}
.finish-node {
  background: url("../../../assets/finish.svg") no-repeat center center;
}
.start-node,
.finish-node {
  cursor: pointer;
  background-size: cover;
}

.node-visited {
  animation: visitedNodeAnimation 1.2s 1 ease-out forwards alternate;
}
@keyframes visitedNodeAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}
.node-shortest-path {
  animation: shortestPath 1.5s ease-out alternate 1 forwards;
}
@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}
.wall {
  background-color: rgba(31, 53, 65, 0.99);
  border-color: rgba(31, 53, 65, 0.9);
}
.wall-visited {
  animation: wallAnimation 0.7s 1 ease-out forwards alternate;
}
@keyframes wallAnimation {
  0% {
    transform: scale(0.3);
  }
  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
